import React, { Component } from 'react';
import logo from './logo.svg';
import './App.css';

import "./sass/app.scss"
// No es necesario poner la extención del archivo
import TopSection from "./components/top/index";
import BotttomSection from "./components/bottom/index";
import BottomSection from './components/bottom/index';

import axios from "axios";
import Weather from './components/top/weather';


const WEATHER_KEY = "c672a564ded842b4be7104728190203";


class App extends Component {
  constructor(props){
    super(props);
    this.state = {
      cityName: "Mexico",
      forecastDays: 4,
      isLoading: true
    };
  }

  updateWeather(){
    const { cityName, forecastDays } = this.state;

    const URL = `https://api.apixu.com/v1/forecast.json?key=${WEATHER_KEY}  &q=${cityName} City&days=${forecastDays}`;
    axios.get(URL).then((res) => {
      return res.data;
    }).then((data) => {
      this.setState({ 
        isLoading: false,
        temp_c: data.current.temp_c, 
        isDay: data.current.is_Day, 
        text: data.current.condition.text, 
        iconURL: data.current.condition.icon,
        forecastdays: data.forecast.forecastday
      });
    }).catch((err) => {
      if(err)
        console.error("Cannot fetch weather Data from API, ", err);
    });
  }

  componentDidMount(){
    const { eventEmitter } = this.props;

    this.updateWeather();

    eventEmitter.on("updateWeather", data => {
      this.setState({ cityName: data }, () => this.updateWeather());
    });
  }


  render() {

    const { isLoading, cityName, temp_c, isDay, text, iconURL, forecastdays } = this.state;



    // MAIN CONTAINER
    return (
    <div className="app-container">
      <div className="main-container">
        {isLoading && <h3>Loading Weather...</h3>}
        {!isLoading &&
        <div className="top-section">
          <TopSection 
          location={cityName} 
          temp_c={temp_c} 
          isDay={isDay} 
          text={text} 
          iconURL={iconURL} 
          eventEmitter={this.props.eventEmitter} />
        </div>}
        <div className="bottom-section">
          <BottomSection forecastdays={forecastdays}/>
        </div>
      </div>
    </div>
    );
  }
}

export default App;
